<template>
  <div>
    {{ msg }}
    <br />
    {{ myobj1 }}
    <br />
    {{ name }} / {{ age }}
    {{ foo }}
    <button @click="chg1">변경1</button>
    <button @click="chg2">변경2</button>
    <button @click="msg++">변경222</button>

    <button @click="emit('change')">change</button>
    <button @click="del">del</button>
  </div>
</template>

<script setup>
import { reactive, ref, toRefs } from 'vue'

const msg = ref('1111111')
const myobj1 = reactive({ name: '네버다이', age: 111 })
//const myobj1 = { name: '네버다이', age: 111 }
const { name, age } = toRefs(myobj1)

const chg1 = () => {
  msg.value = '22222222222'
}

const chg2 = () => {
  myobj1.name = 'ㄷㄷㄷ'
}

const props = defineProps({
  foo: String
})

const emit = defineEmits(['change', 'del'])
// defineExpose({ ...toRefs(myobj1) })

/*
const change = () => {
  console.log('change ddddddd')
  emit('change')
}
*/

const del = () => {
  console.log('del ddddddd')
  emit('del')
}
</script>

